.dianping_list{
    //我的点评
    a{
        display: block;
        padding: 10px 0.3rem;
        dt{
            line-height: 23px;
            .name{
                color: #af874d;
            }
        }
        dd{
            p.text{
                line-height: 18px;
                height: 36px;
                @include line_over;
            }
            p.time{
                line-height: 17px;
                i.iconfont{
                    font-size: 12px;
                }
            }
            .pic{
                padding: 8px 0;
                .lazy{
                   width: 1.8rem;
                   height: 1.8rem;
                   margin-right: 0.2rem;
                   &:nth-child(3n){
                       margin-right: 0;
                   }
                }
            }
        }
    }
}